#app {
    height: 100%;
  }
  html,
  body {
    position: relative;
    height: 100%;
  }
  
  body {
    background: #eee;
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    font-size: 14px;
    color: #000;
    margin: 0;
    padding: 0;
  }
  
  .swiper {
    width: 100%;
    height: 100%;
    margin:0;
    padding:0;
  }
  
  .swiper-slide {
    text-align: center;
    background: #fff;
    position:relative;
    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
    
   
  .videodiv{
    width: 100%;
    height: 15vh;
    font-size: 28px;
    font-weight: 100;
    position: absolute;
    top:70%;
    z-index: 10;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    color:white;
    div{
      width:100%;
      text-align: center;
    } 
    button{
      width:250px;
      height:50px;
      background: black;
      color:white;
      transition: .5s;
      border:none;
      &:hover{
        cursor:pointer;
        background: #b70826;
      }
    }
   
  
  }